<template>
  <div class="cms-list-layout">
      <section class="cms-list-query">
        <div class="query-group">
          <label>sku编号</label>
          <el-input v-model="queryParams.skuCode"></el-input>
        </div>
        <div class="query-group">
           <label>key</label>
          <el-input v-model="queryParams.keyValue"></el-input>
        </div>
         <div class="query-group">
           <label>状态</label>
           <el-select v-model="queryParams.status" style="width:200px;">
            <el-option 
            v-for="(item,index) in options"
            :key="index" 
            :label="item.label" 
            :value="item.value">
            </el-option>
            </el-select>
         </div>
         <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
          <cms-button size="small" :colorPicker="true">
            <i class="iconfont icon-Excel"></i>导出
          </cms-button>
         </div>
       </section>
      <section class="cms-batch">
          <cms-button icon="error" type="error" size="small" @click="deleteDatas(ids)">批量删除</cms-button>
      </section>
      
      <el-table :data="tableData" style="width: 100%" stripe  @selection-change='getCheckItems' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60" align="center"></el-table-column>
      <el-table-column prop="skuCode" label="sku编号" width="260" align="center">
        <template slot-scope="scope">
          <div class="text-overflow">{{scope.row.skuCode}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="keyValue" label="key" width="260" align="center">
        <template slot-scope="scope">
          <div class="text-overflow">{{scope.row.keyValue}}</div>
        </template>
      </el-table-column>
      <el-table-column label="状态" width="260" align="center">
         <template slot-scope="scope">
           {{scope.row.status | statusChange}}
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='255' fixed="right" align="center">
        <template slot-scope="scope">
          <cms-button icon='edit' size='small' type='info'>编辑</cms-button>
          <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">移除</cms-button>
        </template>
      </el-table-column>
    </el-table>
     <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>

<script>
import list from '@/mixins/list'
import { productVirtualCardDataPage, productVirtualCardDataDelete } from '@/api/product/product.js'
var defaultData = {
  'page': '',
  'size': '',
  'tplId': '',
  'status': '',
  'keyValue': '',
  'skuCode': '',
  'storeId': ''
}
export default {
  mixins: [list],
  data() {
    return {
      tableData: [
        {
          id: '',
          skuCode: '',
          keyValue: '',
          status: ''
        }
      ],
      options: [{
        value: 0,
        label: '全部'
      }, {
        value: 1,
        label: '未使用'
      },
      {
        value: 2,
        label: '已使用'
      }],
      value: '',
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    // changeData() {
    //   this.queryParams.id = this.$route.query.id
    //   productVirtualCardDataPage(this.queryParams)
    //     .then(res => {
    //       if (res.code === 200) {
    //         this.tableData = res.data.content
    //         console.log(res.data)
    //       }
    //     })
    // }
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(productVirtualCardDataDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(productVirtualCardDataDelete, ids) // 这个才是真实的删除方法
    }
  },
  filters: {
    statusChange(val) {
      if (val === 1) {
        return '未使用'
      } else {
        return '已使用'
      }
    }
  },
  created() {
    this.queryParams.tplId = this.$route.query.id
    this.getTableData(productVirtualCardDataPage, this.queryParams)
  }
}
</script>

<style>

</style>



