<template>
<div>
  <cms-breadcrumb to="/product/virtualCard/list">
    <li>{{$route.name}}</li>
  </cms-breadcrumb>
<div class="cms-list-layout">    
   <cms-prompt>
      <li>显示卡密模板信息,可对模板进行新增、编辑、删除及添加数据操作</li>
    </cms-prompt>
    <el-button type="success" size="small" style="margin:5px;" @click="$routerLink('/product/virtualCard/dataSave','dataSave',{id:$route.query.id})">添加数据</el-button>

    <el-tabs v-model="activeName" v-if="status" ref='productTab'>
      <el-tab-pane label="全部" name="first">
        <all ref='all' @change="eventChange"></all>
      </el-tab-pane>
      <el-tab-pane label="未使用" name="second" lazy>
        <raw ref="raw"  @change="eventChange"></raw>
      </el-tab-pane>
      <el-tab-pane label="已使用" name="third" lazy>
        <used ref="used" @change="eventChange"></used>
      </el-tab-pane>
    </el-tabs>
  </div>
</div>
</template>

<script>
import all from './all'
import raw from './raw'
import used from './used'
export default {
  components: {
    all,
    raw,
    used
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {
    eventChange(ev) {
      // 切换数据接口
      if (ev === 'all') {
        if (this.$refs['all']) {
          this.$refs['all'].changeData() // 调用列表更新
        }
      }
      if (ev === 'raw') {
        if (this.$refs['raw']) {
          this.$refs['raw'].changeData() // 调用列表更新
        }
      }
      if (ev === 'used') {
        if (this.$refs['used']) {
          this.$refs['used'].changeData() // 调用列表更新
        }
      }
    }
  }
}
</script>

<style>

</style>



